.container {
  display: flex;
  width: 100%;
  flex-flow: column nowrap;
  margin: 20px;
  padding: 20px;
  background-color: rgb(181, 181, 233);
  border-radius: 30px;
}

.container img {
  border-radius: 30px;
}

.buttons {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.buttons button {
  width: 40%;
  height: 50px;
  font-size: 1.3rem;
  margin: 0 0;
  border-radius: 10px;
  border: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text {
  width: 100%;
  background: rgb(220, 220, 247);
  padding: 15px;
  border-radius: 15px;
}
